// 
import request from '@/utils/request'

export interface User { // export
    'user': {
        username: string
        email: string
        password: string
    }
}

export interface RegisterData {
    'user': {
        email: string
        token: string
        username: string
        bio: string
        image: string
    }
}

// 
export const register = async (data: User): Promise<RegisterData> => { // <>asyncPromise
  // 
  const res = await request({
    method: 'POST',
    url: '/users',
    // 
    data
  })
  return res.data
}

